/**
 * 数据埋点相关工具函数
 */

import { v4 as uuidV4 } from 'uuid';
import Device from 'plugins/Device';
import axios from 'axios';
import APIs from 'config/api.config.js';

function getValue (value) {
  return value && typeof value === 'string'
    ? (value.toUpperCase() === 'UNKNOW'
        ? null
        : value)
    : null;
}

/**
 * 创建uuid
 */
export function createUuid () {
  return uuidV4();
}

/**
 * 获取设备信息
 */
export function getDeviceInfo () {
  const d = new Device();

  return d;
}

/**
 * 参数收集器
 * @param { object } info
 * @param { string | null } info.movement
 * @param { string | null } info.pageName
 * @param { string | null } info.location
 * @param { string | null } [info.sysName]
 * @param { string | null } [info.movementInfo]
 * @param { object } deviceInfo
 * @param { object } browserInfo
 * @param { string } uuid
 * 
 * @return { object } stat params object
 */
export function paramsCollector (info, deviceInfo, browserInfo, uuid) {
  const {
    movement,
    pageName,
    location,
    sysName = '东莞市电子证照公示查验系统',
    movementInfo = null
  } = info;

  return {
    // 浏览器
    browser: getValue(browserInfo.browser),
    // 浏览器版本
    browserVersion: getValue(browserInfo.version),
    // cookie的uuid
    cookieUuid: getValue(uuid),
    // 当前页面url    
    currentUrl: getValue(window.location.href),
    // 设备品牌
    equipBrand: getValue(deviceInfo.brand),
    // 设备类型(e.g. 'PC'/'MB')
    // required
    equipType: getValue(deviceInfo.device) === 'Mobile' ? 'MB' : getValue(deviceInfo.device),
    // 语言
    language: getValue(deviceInfo.language),
    // 具体位置(e.g. '下载')
    // required
    location: getValue(location),
    // 具体位置(e.g. 'click'/'visit'/'scroll')
    // required
    movement: getValue(movement),
    // 操作附带的信息(e.g. 搜索时的关键字：'食品')
    movementInfo: getValue(movementInfo),
    // 操作系统
    os: getValue(deviceInfo.os),
    // 操作系统版本
    osVersion: getValue(deviceInfo.osVersion),
    // 页面名称(e.g. '证照详情')
    // required
    pageName: getValue(pageName),
    // 来源页面url
    referrerUrl: getValue(document.referrer),
    // 系统名称(e.g. '东莞市电子证照公示查验系统')
    // required
    sysName: sysName,
  }
}

/**
 * 数据上报
 * @param { object } data
 */
export function reportStat (data) {
  return axios.post(APIs.STATS, data);
}