/**
 * getters
 */

export default {
  // 获取uuid
  uuid (state) {
    return state.stat.uuid
  },

  // ip (state) {
  //   return state.stat.ip
  // },

  // 获取设备信息
  deviceInfo (state) {
    try {
      return state.stat.deviceInfo
    } catch {
      return {}
    }
  },

  // 获取当前浏览器信息
  browserInfo (state) {
    try {
      return state.stat.deviceInfo.browserInfo
    } catch {
      return {}
    }
  }
}