/**
 * 数据埋点 actions
 */

import {
  UUID,
  CHANGE_UUID,
  CHANGE_ADDRESS,
  CHANGE_DEVICE
} from 'config/constants';

export default {
  changeUuid ({ commit }, uuid) {
    commit(CHANGE_UUID, uuid);

    try {
      // 存储到本地
      localStorage.setItem(UUID, uuid);
    } catch {}
  },

  // changeIP ({ commit }, IP) {
  //   commit(CHANGE_IP, IP);
  // },

  changeDevice ({ commit }, deviceInfo) {
    commit(CHANGE_DEVICE, deviceInfo);
  }
}