import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    meta: {
      pageName: '查验主页'
    },
    component: () => import(/* webpackChunkName: "home" */ '../views/Home.vue')
  },

  {
    path: '/license/:id',
    name: 'License',
    meta: {
      pageName: ''
    },
    component: () => import(/* webpackChunkName: "license" */ '../views/License.vue')
  },

  {
    path: '/permit/:id',
    name: 'Permit',
    meta: {
      pageName: '证照详情'
    },
    component: () => import(/* webpackChunkName: "permit" */ '../views/Permit.vue')
  },

  {
    path: '/wechatGuide',
    name: 'WechatGuide',
    meta: {
      pageName: '微信指引'
    },
    component: () => import(/* webpackChunkName: "wechatGuide" */ '../views/WechatGuide.vue')
  },
  {
    path: '/qr',
    component: () => import('../App.vue'),
    children: [
      {
        path: '',
        component: () => import(/* webpackChunkName: "404" */ '../views/404.vue')
      },
      {
        path: 'c',
        name: 'QrPermit',
        meta: {
          pageName: '证照详情（扫码页面）'
        },
        component: () => import(/* webpackChunkName: "qrpermit" */ '../views/QrPermit.vue')
      },
      {
        path: 'scan',
        name: 'QrScan',
        meta: {
          pageName: '公众号扫码'
        },
        component: () => import(/* webpackChunkName: "qrscan" */ '../views/QrScan.vue')
      }
    ]
  },
  {
    path: '*',
    name: '404',
    component: () => import(/* webpackChunkName: "404" */ '../views/404.vue')
  }
];

const router = new VueRouter({
  mode: 'history',
  // base: process.env === 'production' ? '/h5' : process.env.BASE_URL,
  base: '/h5',
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }
});

export default router;
