/**
 * 路由导航守卫
 */

import router from './index';
import NProgress from 'nprogress'; // progress bar
import 'nprogress/nprogress.css'; // progress bar style
import store from '../store';

import { MessageBox } from 'element-ui'

import {
  createUuid,
  getDeviceInfo,
  paramsCollector,
  reportStat
} from 'utils/stat';

import {
  STAT_ACTION_INFO
} from 'config/stat.config.js';

NProgress.configure({ showSpinner: false }); // NProgress Configuration

// 全局前置导航守卫
router.beforeEach((to, from, next) => {
  NProgress.start();

  let {
    getters: {
      uuid,
      // ip,
      deviceInfo
    }
  } = store;

  // 当用户没有特定的uuid时，生成一个存储到本地
  if (!uuid) {
    uuid = createUuid();
    store.dispatch('changeUuid', uuid);
  }

  // 获取当前设备信息
  if (!deviceInfo) {
    deviceInfo = getDeviceInfo();
    store.dispatch('changeDevice', deviceInfo);
  }

  // 当前浏览器环境非 MB 移动设备时
  // 跳转到PC端页面
  if (deviceInfo.device.toUpperCase() !== 'MOBILE' && deviceInfo.device.toUpperCase() !== 'MB') {
    if (process.env.NODE_ENV === 'production') {
      const {
        protocol,
        host,
        origin
      } = window.location
      window.location.href = `${protocol}//${host ? host : origin}`;
      return false;
    }
  }

  // 浏览器数据
  const { browserInfo = {} } = deviceInfo;

  // 当前进入的页面名称
  const pageName = (to.meta && to.meta.pageName) ? to.meta.pageName : null;

  // 是否为扫码进入的页面
  const isScan = navigator.userAgent.toLowerCase().indexOf('micromessenger') > -1;

  // 证照详情页面
  // 1. 微信环境下扫码需要跳转到展示页面
  // 2. 非微信环境下的扫码暂时允许用户直接访问
  // console.log(to.name)
  // if (to.name === 'Permit' && isScan) {
  //   if (browserInfo.browser && browserInfo.browser.toUpperCase() === 'WECHAT') {
  //     // 微信环境
  //     next({
  //       // 进入微信指引页面
  //       name: 'WechatGuide'
  //     })
  //   } else {
  //     // 非微信环境
  //   }
  // }

  if (pageName) {
    // 上报用户访问数据
    const info = isScan ? STAT_ACTION_INFO.SCAN : STAT_ACTION_INFO.VISIT;
    info.pageName = pageName;
    info.sysName = '东莞市电子证照公示查验系统';
    
    const data = paramsCollector(info, deviceInfo, browserInfo, uuid);
 
    reportStat(data); // 不考虑上报失败情况
  }

  next();
});

// 全局后置导航守卫
router.afterEach(() => {
  NProgress.done();
})