/**
 * Vue指令
 * 埋点指令
 */


import store from '@/store';
import {
  STAT_ACTION,
  STAT_ACTION_INFO
} from 'config/stat.config.js';
import {
  paramsCollector,
  reportStat
} from 'utils/stat';

// 处理器
function handler (el, binding) {
  const { value } = binding;

  if (!value) { // value不存在，无法识别的操作
    return false;
  }

  const action = value.action; // 当前的操作

  const info = STAT_ACTION[action]
    ? STAT_ACTION_INFO[STAT_ACTION[action]] || null
    : null;

  if (!info) { // 无法识别的操作
    return false;
  }

  const {
    getters: {
      deviceInfo,
      browserInfo,
      uuid
    }
  } = store;

  info.movementInfo = value.movementInfo;

  const data = paramsCollector(info, deviceInfo, browserInfo, uuid);

  reportStat(data); // 数据上报，不考虑上报失败的处理
}

const stat = {
  bind (el, binding) {
    el._bindEvent = handler.bind(this, el, binding);
    el.addEventListener('click', el._bindEvent)
  },

  update (el, binding) {
    if (el._bindEvent) {
      el.removeEventListener('click', el._bindEvent)
    }
    el._bindEvent = handler.bind(this, el, binding);
    el.addEventListener('click', el._bindEvent)
  }
}

export default stat;