/**
 * 工具函数
 */

/**
 * 延时函数
 */
export function sleep (delay) {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve();
    }, delay);
  })
}

/**
 * 模拟enum
 * @param { object } object - 需要转换成枚举类型的对象
 * @return { object }
 */
export function objectToEnum (object) {
  const entries = Object.entries(object);

  if (entries.length <= 0) {
    return null;
  }

  let temp = {};

  for (let [key, value] of entries) {
    temp[key] = value;
    temp[value] = key;
  }

  return Object.freeze(temp);
}

/**
 * 获取滚动条距离
 * getScrollOffset
 * 
 * @return { { left: number, top: number } } 返回一个包含垂直和水平方向滚动条的距离的对象
 * 
 */
export function getScrollOffset () {
  if (window.pageXOffset) { // 如果window.pageXOffset存在，则使用
    return {
      left: window.pageXOffset,
      top: window.pageYOffset
    };
  } else {
    // 由于document.documentElement.scrollLeft与document.body.scrollLeft有下面这种现象
    // 一个有值，另一个为0
    // 所以可以将两者相加来取值
    return {
      left: document.documentElement.scrollLeft + document.body.scrollLeft,
      top: document.documentElement.scrollTop + document.body.scrollTop
    }
  }
}

/**
 * 获取浏览器可视区域（窗口）的尺寸
 * getViewportSize
 * 
 * @return { { width: number, height: number } } 返回一个包含了浏览器可视区域（窗口）的尺寸的对象
 *
 */
export function getViewportSize () {
  if (window.innerWidth) {
    return {
      width: window.innerWidth,
      height: window.innerHeight
    }
  } else {
    if (document.compatMode === 'BackCompat') {
      // 怪异模式
      return {
        width: document.body.clientWidth,
        height: document.body.clientHeight
      }
    } else {
      // 标准模式
      return {
        width: document.documentElement.clientWidth,
        height: document.documentElement.clientHeight
      }
    }
  }
}

/**
 * 获取页面（HTML文档）尺寸
 * getScrollSize
 * 
 * @return { { width: number, height: number } } 返回一个包含页面尺寸的对象
 * 
 */
export function getScrollSize () {
  if (document.body.scrollWidth) {
    return {
      width: document.body.scrollWidth,
      height: document.body.scrollHeight
    }
  } else {
    return {
      width: document.documentElement.scrollWidth,
      height: document.documentElement.scrollHeight
    }
  }
}

/**
 * 元素到浏览器左上角的距离
 * 
 * @param { HTMLElement } el 需要查询的元素节点
 * @return { { left: number, top: number } } 返回一个包含了Left和Top距离的对象
 * 
 */
export function getElementDocPosition (el) {
  var parentNode = el.offsetParent, // 最近的，有定位属性的祖先元素
      left = el.offsetLeft,
      top = el.offsetTop;

  while (parentNode) { // 如果存在父级定位元素，则加上父级定位元素的offset值
    left += parentNode.offsetLeft + parentNode.clientLeft;
    top += parentNode.offsetTop + parentNode.clientTop;
    parentNode = parentNode.offsetParent;
  }

  return {
    left: left,
    top: top
  }
}

/**
 * 获取元素计算样式
 * getStyles
 * 
 * @param { HTMLElement } elem 需要获取样式的节点
 * @param { string } [prop] 可选，样式属性
 * @return { object | string } 返回一个样式集合或是某个属性的样式
 */
export function getStyles (elem, prop) {
  var styles;
  if (window.getComputedStyle) {
    styles = window.getComputedStyle(elem, null);
  } else { // IE8及以下
    styles = elem.currentStyle;
  }

  if (prop) {
    return styles[prop];
  } else {
    return styles;
  }
}